	import http from '../../utils/http.js'
	import api from '../../utils/api.js'
	export default {
		data() {
			return {
				countdown:'获取验证码',
				timestatus:false,
				disabled:false,
				yanPhone:'',
				code:'',
			}
		},
		methods: {
			// 注册
			register(){
				uni.navigateTo({
					url: '../enterpriseRegister/enterpriseRegister'
				})
			},
			// 获取验证码
			getCode(){
				let self=this;
				if(self.yanPhone==''){
					uni.showToast({
						icon:'none',
						title: '请填写手机号码'
					});
					return
				}else{
					self.getSmsCode()
					
				}
			},
			// 验证验证码
			phoneSubmit(){
				let self=this;
				if(self.code==''){
					uni.showToast({
						icon:'none',
						title: '请输入手机验证码'
					});
					return
				}
				let param={
					code:self.code,
					phone:self.yanPhone,
				}
				http.Request(api.checkSmsCode,'POST',param,function(res){
					// self.loading=false
					console.log(res)
					uni.setStorageSync('phone', self.yanPhone);
					self.phone= self.yanPhone;
					self.phoneMask=false;
					self.yanPhone='';
				})
			},
			// diao用获取验证码接口
			getSmsCode(){
				let self = this;
				let param={
					mobile:self.yanPhone,
				}
				http.Request(api.getSmsCode,'POST',param,function(res){
					// self.loading=false
					console.log(res)
					self.disabled = true;//禁用点击
					self.countdown=60;
					self.timestatus=true;
					self.clear=setInterval(self.countDown,1000)
				})
			},
			// 倒计时
			countDown(){
				let self = this;
				if(!self.countdown){
					self.disabled = false;
                    self.timestatus = false;
                    self.countdown = '获取验证码';
                    clearInterval(self.clear);
				}else{
					--self.countdown;
				}
			}
		}
	}
